.profile__subtitle {
  margin: 0;
  max-width: 300px;
  font-family: 'Inter', 'Arial', 'sans-serif';
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0em;
  padding-top: 8px;
  text-align: left;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 650px) {
  .profile__subtitle {
    font-size: 14px;
    line-height: 17px;
    padding: 10px 0 32px 0;
    margin: auto;
    max-width: 282px;
  }
}