.footer__text {
  font-family: 'Inter', 'Arial', 'sans-serif';
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0em;
  color: #545454;
  margin: 0;
}

@media screen and (max-width: 550px) {
  .footer__text {
    max-width: 282px;
    font-size: 14px;
    line-height: 17px;
  }
}